import { Badge } from "@/components/ui/badge";

interface ExperienceItemProps {
  period: string;
  title: string;
  company: string;
  description: string;
  technologies: string[];
}

function ExperienceItem({ period, title, company, description, technologies }: ExperienceItemProps) {
  return (
    <div className="mb-12">
      <div className="text-muted-foreground mb-2">{period}</div>
      <h3 className="text-lg font-medium mb-1">{title} - {company}</h3>
      <p className="text-muted-foreground mb-4 max-w-3xl">{description}</p>
      <div className="flex flex-wrap gap-2">
        {technologies.map((tech) => (
          <Badge key={tech}>{tech}</Badge>
        ))}
      </div>
    </div>
  );
}

